@import "mixins";
@import "colors";


.App {
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
 
}

.Dark {
  .Background {
    background-color: $DarkBackgroud;
    @include NoDefault;
  }

  .Element {
    @include Elements($DarkElements, $Dark-Text);
  }

  .ThemeTextColor {
    color: $Dark-Text;
    caret-color: $Dark-Text;
  }
}

.Light {
  .Background {
    background-color: $LightBackground;
    @include NoDefault;
  }

  .Element {
    @include Elements($LightElements, $Light-Text);
  }

  .ThemeTextColor {
    color: $Light-Text;
    caret-color: $Light-Text;
  }
}

.Flex {
  display: flex;
  justify-content: space-between;
}

.Title {
  font-size: 24px;
  font-weight: 800;
  width: 300px;
  padding: 1% 10%;
}

.ThemeChangerButton {
  margin: 2.5% 5%;
  width: 150px;
}
.ThemeChangerButton:hover {
  color: aquamarine;
}

.ThemeName {
  display: inline;
  margin: 15px;
}

.SearchBar {
  width: 30%;
  margin: 2.5% 10%;
  border-radius: 5px;
}
.SearchInput {
  width: 100%;
  background-color: transparent;
  border: none;
  opacity: 0.5;
}

.SearchInput:focus {
  outline: none;
  border: none;
  opacity: 1;
}

.SearchIcon {
  margin: 20px;
}

.Filter {
  width: 10%;
  margin: 2.5% 10%;
  border: 5px;
}

.FilterButton {
  display: flex;
  align-content: center;
  padding: 2.5% 5%;
  font-size: 16px;
}

.FilterText {
  display: inline;
  width: 80%;
}
.FilterIcon {
  margin-top: 5%;
  display: inline;
}

.TopLayer {
  position: absolute;
  z-index: 1;
  width: 300px;
  margin-left: -100px;
}

.visiblityOff {
  visibility: hidden;
}

.Border-R {
  border-radius: 5px;
}

.RegionList {
  margin: 2% 0;
  list-style: none;
  padding: 10px 0 10px 20px;
}

.Region {
  width: 100%;
  padding: 5px 0 5px;
}

.FlagContainer {
  flex-wrap: wrap;
  justify-content: center;
  gap: 55px 55px;
  margin: 0% 8.5%;
}
.CardContainer {
  width: 325px;
  height: 500px;
  padding: 5px;
}

.FlagImg {
  width: 100%;
  height: 200px;
  object-fit: scale-down;
}

.InformationContainer {
  margin: 0 0 50px 25px;
}
h3 {
  font-weight: 300;
}

.CountyName {
  font-weight: 800;
}
// Details Page

.Detailstitle {
  font-weight: 700;
}

.DetailsInfo {
  font-weight: 300;
}

.DetailsBlock {
  margin-top: 2.5%;
  width: 50%;
  display: inline-block;
}

.DetailsFlag {
  min-width: 40%;
  display: inline;
  vertical-align: top;
}
.DetailsContainer {
  margin: 10%;
}

.BorderButtonContainer {
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  gap: 15px 50px;
  margin-bottom: 5%;
}

.BorderButton {
  text-align: center;
  min-width: 100px;
  padding: 5px 15px;
  height: 25px;
}
.BorderButton:hover{
  background-color: aquamarine;
  font-weight: 800;
}

.BorderButtonContent {
  margin: 0;
}

.BackButton {
  padding: 15px 50px;
  margin-bottom: 5%;
  display: block;
  max-width: 100px;
}
.Back {
  display: inline-block;
}

.DetialsTextContainer {
  width: 50%;
  margin-left: 5%;
  display: inline-block;
}

.DetailsName{
  margin-top: 0;
}
// Details Page

@media only screen and(max-width: 1000px) {
  .SearchBar {
    width: 100%;
  }

  .Wrap {
    flex-wrap: wrap;
    margin-bottom: 25%;
  }

  .TopLayer {
    margin: 0;
  }

  //Details Page

  .DetailsName{
    margin-top: .67em;
  }
  .BackButton {
    padding: 5px 25px;
  }

  .DetialsTextContainer {
    width: 100%;
    margin-left: 0;
  }
  .DetailsBlock {
    margin-top: 5%;
    display: block;
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
.Title{
  font-size: 16px;
 }
 .ThemeName {
  font-size: 12px;
  margin: 5px;
 }
 .ThemeChangerButton{
  margin: 4% ;
 }


  .DetailsBlock {
    margin-top: 15%;
  }

  .BorderButton {
    font-size: 12px;
    min-width: 75px;
  }
  

  .BorderButtonContainer {
    justify-content: flex-start;
    width: 100%;
    gap: 5px 15px;
  }
  .DetailsFlag {
    width: 100%;
    display: inline;
  }

  .TopLayer {
    width: 225px;
  }
  

}


@media only screen and (max-width: 500px) {


  .Title{
    font-size: 12px;
   }
   .ThemeName {
    font-size: 8px;
    margin: 5px;
   }
   .ThemeChangerButton{
    margin: 4% ;
   }

}